import React from 'react';
import './Partners.css';
import ServicesSlider from '../ServicesSlider/ServicesSlider';

const Partners = () => {
    return(
        <section>
            <div className="banner">
                <p className="heroText">Partners</p>
            </div>
            
            <div className="cardContainer">
                <ServicesSlider time={3000}>
                    <div className="partner">
                        <p>Logo1</p>
                    </div>
                    <div className="partner">
                        <p>Logo2</p>
                    </div>
                    <div className="partner">
                        <p>Logo3</p>
                    </div>
                    <div className="partner">
                        <p>Logo4</p>
                    </div>
                </ServicesSlider>
            </div>
        </section>
    )
}

export default Partners;